* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
}
html,
body,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

